//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.our_projects_info {
		width: 100%;
		height: 536px;
		margin-top: -18px;
		background: url("../img/content/All_projects_bg.jpg") no-repeat center;
		border-top: 2px solid $arrowcolor;
		position: relative;
		z-index: 100;
	}

	.our_projects_info_title {
		display: inline-block;
		margin-top: 50px;
		margin-left: 15px;
		height: 34px;
		width: 290px;
		color: #fff;
		font-family: $desk-bold-raleway;
		font-size: 24px;
		line-height: 34px;
	}

	.our_projects_cards_info {
		background: transparent;
		display: flex;
		position: absolute;
		top: 101px;
		left: 15px;
		width: 290px;
		height: 420px;
		justify-content: space-between;
		flex-wrap: wrap;
	}

	.our_projects_cards_item {
		position: relative;
		background: #fff;
		width: 125px;
		height: 130px;
		box-shadow: 4px 4px 24px 0 rgba(0, 0, 0, 0.15);
		cursor: pointer;
		z-index: 10;

		.our_projects_cards_item_title {
			display: inline-block;
			margin-top: 27px;
			margin-left: 30px;
			margin-bottom: 0px;
			width: 51px;
			min-width: 51px;
			height: 32px;
			width: 33px;
			color: #333333;
			font-family: $desk-bold-proxima;
			font-size: 24px;
			line-height: 34px;
		}

		.our_projects_cards_description {
			display: inline-block;
			margin-left: 7px;
			margin-top: -15px;
			height: 40px;
			width: 113px;
			opacity: 0.3;
			color: #333333;
			font-family: $desk-bold-proxima;
			font-size: 16px;
			white-space: pre-line;
			line-height: 20px;
			text-align: center;
		}
	}

	.cards_item1 .our_projects_cards_item_title {
		margin-left: 50px;
	}

	.cards_item2 .our_projects_cards_item_title {
		margin-left: 35px;
	}

	.cards_item4 .our_projects_cards_item_title {
		margin-left: 42px;
	}

	.cards_item5 .our_projects_cards_item_title {
		width: 139px;
		margin-left: 12px;
	}

	.cards_item6 {
		background: $Bghover-color;
	}

	.cards_item_help {
		display: inline-block;
		margin-top: 56px;
		margin-left: 12px;
		color: #FFFFFF;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 16px;
		position: relative;
		font-style: normal;

		a {
			text-decoration: none;
			color: inherit;
		}
	}

	.cards_item_help::after {
		content: '';
		position: absolute;
		top: 2px;
		left: 90px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right_white.svg") no-repeat center;
	}

	.our_projects_cards_item_second {
		position: relative;
		top: -102px;
		display: none;
		background: #fff;
		width: 125px;
		height: 130px;
		background: $Bghover-color;
		cursor: pointer;
		z-index: 20;
	}
}

