			background-color: #A5A5A5;
		}
	}

	.dacha_center_project h2,
	.existing_main_project h2,
	.done_main_project h2 {
		margin-top: 426px;
		margin-left: 20px;
		color: #333;
		font-family: $desk-bold-raleway;
		font-size: 18px;
		font-weight: bold;
		white-space: nowrap;
		line-height: 26px;
	}

	.dacha_center_project span:nth-of-type(2),
	.existing_main_project span:nth-of-type(2),
	.done_main_project span:nth-of-type(2) {
    margin-top: 30px;
		margin-left: 20px;
		display: inline-block;
    white-space:normal;
		color: #333;
		font-family: $desk-regular-raleway;
		font-size: 14px !important;
		line-height: 24px !important;
	}

	.dacha_center_project a:nth-of-type(1),
	.existing_main_project a:nth-of-type(1),
	.done_main_project a:nth-of-type(1) {
		margin-top: 60px;
		margin-left: 21px;
    position:relative;
		text-decoration: none;
		color: $arrowcolor;
		font-family: $desk-bold-raleway;
		font-size: 14px;
    white-space:nowrap;
		font-weight: normal;
		line-height: 17px;
	}

	.dacha_center_project a:nth-of-type(1)::after,
	.existing_main_project a:nth-of-type(1)::after,
	.done_main_project a:nth-of-type(1)::after {
		content: '';
		position: absolute;
		top: 2px;
		left: 85px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.dacha_center_project_received,
	.existing_main_project_received,
	.done_main_project_received {
		position: absolute;
    margin:0;
		top: 235px;
		left: 44px;
		height: 20px;
		width: 107px;
		color: #fff;
		font-family: $desk-bold-proxima;
		font-size: 16px;
		font-weight: normal;
		white-space: nowrap;
		line-height: 18px;
	}

	.dacha_center_project b,
	.existing_main_project b,
	.done_main_project b {
		position: absolute;
		top: 200px;
		right: 0px;
    left:44px;
    margin:0;
		font-weight: normal;
		height: 19px;
		width: 69px;
		color: #fff;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 18px;
	}

	.dacha_center_project_total,
	.existing_main_project_total,
	.done_main_project_total {
		position: absolute;
    margin:0;
		top: 235px;
		right: 32px;
		height: 20px;
		width: 119px;
		color: $arrowcolor;
		font-family: $desk-bold-proxima;
		font-size: 16px;
		font-weight: normal;
		white-space: nowrap;
		line-height: 20px;
	}

	.dacha_center_project_total::after,
	.dacha_center_project_received::after {
		content: '.';
	}

	.existing_main_project_total::after,
	.existing_main_project_received::after {
		content: '.';
	}

	.done_main_project_total::after,
	.done_main_project_received::after {
		content: '.';
	}

	.dacha_center_progress,
	.existing_main_project_progress,
	.done_main_project_progress {
		position: absolute;
    margin:0;
		left: 44px;
		top: 283px;
		width: 423px;
		height: 1px;
		background-color: #A5A5A5;
		border: none; /* Needed for Firefox */
		color: #A5A5A5; /* For IE10 */
	}

	.other_projects_list,
	.existing_projects,
	.done_projects {
		display: flex;
		position: relative;
		width: 750px;
		height: 1240px;
		margin: auto;
		flex-wrap: wrap;
		justify-content: space-between;

		@include progressiveRules;

		div {
			position: relative;
			height: 580px;
			width: 353px;
			white-space: nowrap;
			background-color: #FFF;
			box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

			b {
				position: absolute;
				top: 159px;
				right: 240px;
				height: 19px;
				width: 69px;
				color: #fff;
				font-family: 'Raleway-Regular', sans-serif;
				font-size: 16px;
				font-weight: normal;
				line-height: 19px;
			}

			span:nth-of-type(1) {
				display: inline-block;
				position: absolute;
				top: 183px;
				right: 213px;
				height: 20px;
				width: 96px;
				color: #FFF;
				font-family: "ProximaNova-Bold", sans-serif;
				font-size: 16px;
				font-weight: bold;
				line-height: 20px;
			}

			span:nth-of-type(2) {
				position: absolute;
				top: 183px;
				right: 22px;
				height: 20px;
				width: 109px;
				color: #E78B48;
				font-family: "ProximaNova-Bold", sans-serif;
				font-size: 16px;
				font-weight: bold;
				line-height: 20px;
			}
		}
	}

	.additional_project1,
	.additional_project2,
	.additional_project3,
  .additional_project4,
	.additional_project5,
	.additional_project6 {
		display: none;
	}

	.other_projects_list_item3,
	.camp {
		display: none !important;
	}

	.existing_projects .additional_project1,
	.existing_projects .additional_project2,
	.existing_projects .additional_project3,
  .existing_projects .additional_project4,
	.existing_projects .additional_project5,
	.existing_projects .additional_project6{
		display: none;
	}

	.done_projects .additional_project1,
	.done_projects .additional_project2,
	.done_projects .additional_project3,
  .done_projects .additional_project4,
	.done_projects .additional_project5,
	.done_projects .additional_project6 {
		display: none;
	}

	.lviv span:nth-of-type(2) {
		right: 72px !important;
	}

	.other_projects_list_item1 .other_projects_list_item1_total {
		right: 39px !important;
	}

	.other_projects_list div span:nth-of-type(1)::after,
	.other_projects_list div span:nth-of-type(2)::after {
		content: '.';
	}

	.existing_projects div span:nth-of-type(1)::after,
	.existing_projects div span:nth-of-type(2)::after {
		content: '.';
	}

	.done_projects div span:nth-of-type(1)::after,
	.done_projects div span:nth-of-type(2)::after {
		content: '.';
	}

	.other_projects_list_progress {
		position: absolute;
		left: 43px;
		top: 224px;
		width: 273px;
		height: 1px;
		background-color: #A5A5A5;
		color: #A5A5A5;
		border: none;
	}

