		content: '';
		position: absolute;
		top: -1px;
		right: 0;
		height: 3px;
		width: 3px;
		background-color: #E78B48;
	}

	.other_projects_list_item1 span:nth-of-type(3),
	.other_projects_list_item2 span:nth-of-type(3),
	.other_projects_list_item3 span:nth-of-type(3) {
		position: absolute;
		text-transform: uppercase;
		top: 280px;
		left: 40px;
		height: 15px;
		width: 66px;
		color: #A5A5A5;
		font-family: 'Raleway-Regular', sans-serif;
		font-size: 13px;
		line-height: 15px;
	}

	.other_projects_list_item1 span:nth-of-type(3)::after,
	.other_projects_list_item2 span:nth-of-type(3)::after,
	.other_projects_list_item3 span:nth-of-type(3)::after {
		content: '';
		position: absolute;
		bottom: -20px;
		left: 0px;
		width: 273px;
		height: 1px;
		background: #A5A5A5;
		opacity: 0.25;
	}

	.other_projects_list_item1 h2.item_title,
	.other_projects_list_item2 h2.item_title,
	.other_projects_list_item3 h2.item_title {
		margin-top: 78px;
		margin-left: 40px;
		margin-bottom: 20px;
		height: 24px;
		min-width: 186px;
		color: #333333;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 20px;
		line-height: 24px;
		word-spacing: unset;
	}

	.other_projects_list_item1 p.item_title_tip,
	.other_projects_list_item2 p.item_title_tip,
	.other_projects_list_item3 p.item_title_tip {
		height: 78px;
		min-width: 241px;
		margin-left: 40px;
		color: #333333;
		font-family: 'Raleway-Regular', sans-serif;
		font-size: 16px;
		line-height: 26px;
	}

	.other_projects_list_item2 p.item_title_tip {
		display: inline-block;
		margin-top: 20px;
	}

	.lviv p.item_title_tip {
		height: 104px;
		width: 241px;
		white-space: pre-line;
		margin-top: -25px;
	}

	.info p.item_title_tip {
		height: 104px;
		width: 241px;
		white-space: pre-line;
		margin-top: -25px;
	}

	.camp p.item_title_tip {
		height: 104px;
		width: 241px;
		white-space: pre-line;
		margin-top: -25px;
	}

	.other_projects_list_item1 span:nth-of-type(4),
	.other_projects_list_item2 span:nth-of-type(4),
	.other_projects_list_item3 span:nth-of-type(4) {
		position: absolute;
		bottom: 40px;
		left: 41px;
		text-decoration: none;
		color: #e78b48;
		font-family: 'Raleway-Regular',sans-serif;
		font-size: 14px;
		font-weight: 700;
		line-height: 16px;

		a {
			text-decoration: none;
			color: inherit;
		}
	}

	.other_projects_list_item1 span:nth-of-type(4)::after,
	.other_projects_list_item2 span:nth-of-type(4)::after,
	.other_projects_list_item3 span:nth-of-type(4)::after {
		content: '';
		position: absolute;
		top: 2px;
		left: 85px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.other_projects_more,
	.existing_projects_button,
	.done_projects_button {
		margin-top: 8px;
		margin-left: 37%;

		&:hover,
		&:focus {
			opacity: 0.7;
			transition: .4s;
			margin-left: 39%;
		}

		ul {
			display: inline-block;
			width: 50px;
			list-style-type: none;

			li {
				display: inline-block;
				height: 3px;
				width: 3px;
				margin-right: 2px;
				background-color: #E78B48;
			}
		}

		button.to_more_projects {
			display: inline-block;
			margin-left: -28px;
			min-width: 112px;
			background: none;
			outline: none;
			border: none;
			color: #E78B48;
			font-family: 'Raleway-Bold', sans-serif;
			font-size: 14px;
			font-weight: bold;
			word-spacing: 2px;
			line-height: 16px;
			cursor: pointer;
		}
	}

	.existing_main_project,
	.done_main_project {
		display: none;
	}

	.existing_projects,
	.done_projects {
		display: none;
	}

	.existing_projects_button {
		display: none;
	}

	.done_projects_button {
		display: none;
	}

	.existing_projects .lviv,
	.existing_projects .info,
	.existing_projects .camp {
		display: block;
	}

	.done_projects .lviv,
	.done_projects .info,
	.done_projects .camp {
		display: block;
	}

	.display_flex {
		display: flex !important;
	}

	.display_inline_block {
		display: inline-block !important;
	}

	.hide_done_project {
		visibility: hidden;
	}
}

