}

.about_projects_existing_projects {
	left: 25%;
	width: 45px;
}

.about_projects_done_projects {
	left: 40%;
	width: 83px;
}

.dacha_center_project,
.existing_main_project,
.done_main_project {
	display: inline-block;
	margin-top: 240px;
	margin-left: 5%;
	margin-bottom: 40px;
	position: relative;
	height: 394px;
	width: 1000px;
	background-color: #FFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

	@include progressiveRules;

	img:nth-of-type(1) {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 573px;
		height: 100%;
	}

	span:nth-of-type(1) {
		position: absolute;
		text-transform: uppercase;
		top: 30px;
		left: 604px;
		height: 14px;
		width: 61px;
		color: #A5A5A5;
		font-family: 'Raleway-Regular', sans-serif;
		font-size: 12px;
		line-height: 14px;
	}

	span:nth-of-type(1)::after {
     width:360px;  
	}
}

.dacha_center_project h2,
.existing_main_project h2,
.done_main_project h2 {
	display: inline-block;
	margin-top: 96px;
	margin-left: 603px;
	margin-bottom: 0;
	color: #333;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	font-weight: bold;
	white-space: nowrap;
	line-height: 24px;
}

.dacha_center_project span:nth-of-type(2),
.existing_main_project span:nth-of-type(2),
.done_main_project span:nth-of-type(2) {
	display: inline-block;
	margin-top: 30px;
	margin-left: 603px;
	white-space: normal;
	color: #333;
	font-family: 'Raleway-regular', sans-serif;
	font-size: 16px !important;
	line-height: 28px !important; 
}

.dacha_center_project a:nth-of-type(1),
.existing_main_project a:nth-of-type(1),
.done_main_project a:nth-of-type(1) {
  display: inline-block;
	position: relative;
	margin-left: 602px;
	margin-top: 40px;
	text-decoration: none;
	color: #E78B48;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	white-space: nowrap;
	line-height: 16px;
}

.dacha_center_project a:nth-of-type(1)::after,
.existing_main_project a:nth-of-type(1)::after,
.done_main_project a:nth-of-type(1)::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 85px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.dacha_center_project_received,
.existing_main_project_received,
.done_main_project_received {
	display: inline-block;
	margin-top: 34px;
	margin-left: 602px;
	margin-bottom: 5px;
	margin-right: 8px;
	color: #333333;
	font-family: "ProximaNova-Bold", sans-serif;
	font-size: 16px;
	font-weight: bold;
	white-space: nowrap;
	line-height: 20px;
}

.dacha_center_project b,
.existing_main_project b,
.done_main_project b {
 	display: inline-block;
	margin-top: 34px;
	margin-right: 50px;
	font-weight: normal;
	white-space: nowrap;
	color: #333333;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 19px; 
}

.dacha_center_project_total,
.existing_main_project_total,
.done_main_project_total {
	display: inline-block;
	margin-top: 34px;
	color: #E78B48;
	font-family: "ProximaNova-Bold", sans-serif;
	font-size: 16px;
	font-weight: bold;
	white-space: nowrap;
	line-height: 20px;
}

.dacha_center_project_total::after,
.dacha_center_project_received::after {
	content: '.';
}

.existing_main_project_total::after,
.existing_main_project_received::after {
	content: '.';
}

.done_main_project_total::after,
.done_main_project_received::after {
	content: '.';
}

.dacha_center_progress,
.existing_main_project_progress,
.done_main_project_progress {
	display: inline-block;
	position: relative;
	margin-left: 602px;
	width: 360px;
	height: 1px;
	background-color: #A5A5A5;
	border: none; /* Needed for Firefox */
	color: #A5A5A5; /* For IE10 */
}

.other_projects_list,
.existing_projects,
.done_projects {
	display: flex;
	position: relative;
	width: 1080px;
	height: 1240px;
	margin: auto;
	flex-wrap: wrap;
	justify-content: space-between;

	@include progressiveRules;

	div {
		position: relative;
		height: 580px;
		width: 353px;
		white-space: nowrap;
		background-color: #FFF;
		box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

		b {
			position: absolute;
			top: 159px;
			right: 240px;
			height: 19px;
			width: 69px;
			color: #fff;
			font-family: 'Raleway-Regular', sans-serif;
			font-size: 16px;
			font-weight: normal;
			line-height: 19px;
		}

		span:nth-of-type(1) {
			display: inline-block;
			position: absolute;
			top: 183px;
			right: 213px;
			height: 20px;
			width: 96px;
			color: #FFF;
			font-family: "ProximaNova-Bold", sans-serif;
			font-size: 16px;
			font-weight: bold;
			line-height: 20px;
		}

		span:nth-of-type(2) {
			position: absolute;
			top: 183px;
			right: 22px;
			height: 20px;
			width: 109px;
			color: #E78B48;
			font-family: "ProximaNova-Bold", sans-serif;
			font-size: 16px;
			font-weight: bold;
			line-height: 20px;
		}
	}
}

