.other_projects_more,
.existing_projects_button,
.done_projects_button {
	margin-top: 8px;
	margin-left: 443px;
	width: 300px;

	&:hover,
	&:focus {
		opacity: 0.7;
		transition: .4s;
		margin-left: 449px;
	}

	ul {
		display: inline-block;
		width: 50px;
		list-style-type: none;

		li {
			display: inline-block;
			height: 3px;
			width: 3px;
			margin-right: 2px;
			background-color: #E78B48;
		}
	}

	button.to_more_projects {
		display: inline-block;
		margin-left: -28px;
		min-width: 112px;
		background: none;
		outline: none;
		border: none;
		color: #E78B48;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 14px;
		font-weight: bold;
		word-spacing: 2px;
		line-height: 16px;
		cursor: pointer;
	}
}

.existing_main_project,
.done_main_project {
	display: none;
}

